import DashboardLayout from '../../components/layout/DashboardLayout';

export default function AdminDashboard() {
  // Mock data only (no API calls)
  const stats = [
    { title: 'Total Vendors', value: '128' },
    { title: 'Total Customers', value: '2,945' },
    { title: 'Total Orders', value: '7,324' },
    { title: 'Platform Revenue', value: '$183,420' },
  ];

  const recentOrders = [
    { id: 'ORD-8731', customer: 'Priya Sharma', vendor: 'Fresh Mart', total: '$38.60', status: 'Delivered' },
    { id: 'ORD-8729', customer: 'Arjun Mehta', vendor: 'Veggie Hub', total: '$22.10', status: 'Pending' },
    { id: 'ORD-8727', customer: 'Neha Gupta', vendor: 'Daily Needs', total: '$69.00', status: 'Shipped' },
    { id: 'ORD-8725', customer: 'Rahul Jain', vendor: 'Snack Stop', total: '$15.50', status: 'Cancelled' },
  ];

  const onboarding = {
    pendingKyc: 5,
    awaitingApproval: 12,
    activeVendors: 128,
  };

  return (
    <DashboardLayout role="admin">
      <h1 className="adm-title">Admin Dashboard</h1>
      <div className="card-grid">
        {stats.map((s) => (
          <div className="card" key={s.title}>
            <div className="card-title">{s.title}</div>
            <div className="card-value">{s.value}</div>
          </div>
        ))}
      </div>

      <section className="adm-section">
        <h2 className="section-title">Recent Orders</h2>
        <table className="table">
          <thead>
            <tr>
              <th>Order ID</th>
              <th>Customer</th>
              <th>Vendor</th>
              <th>Total</th>
              <th>Status</th>
            </tr>
          </thead>
          <tbody>
            {recentOrders.map((o) => (
              <tr key={o.id}>
                <td>{o.id}</td>
                <td>{o.customer}</td>
                <td>{o.vendor}</td>
                <td>{o.total}</td>
                <td>
                  <span className={`badge ${o.status === 'Pending' ? 'warn' : o.status === 'Cancelled' ? 'danger' : ''}`}>
                    {o.status}
                  </span>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </section>

      <section className="adm-section">
        <h2 className="section-title">Vendor Onboarding</h2>
        <div className="adm-onboarding">
          <div className="card">
            <div className="card-title">Pending KYC</div>
            <div className="card-value">{onboarding.pendingKyc}</div>
          </div>
          <div className="card">
            <div className="card-title">Awaiting Approval</div>
            <div className="card-value">{onboarding.awaitingApproval}</div>
          </div>
          <div className="card">
            <div className="card-title">Active Vendors</div>
            <div className="card-value">{onboarding.activeVendors}</div>
          </div>
        </div>
      </section>
    </DashboardLayout>
  );
}
